body {
  padding: 0;
  margin: 0; 
  /* background: #ADD8E6;   */
  background: no-repeat center center fixed;
  background-image: url(/mandatory-advanced-js4/static/media/image1.64b558c1.jpeg);
  background-size: cover;
}

.form{
  position: absolute; 
  left: 50%; 
  top: 65%;  
  -webkit-transform: translate(-50%, -50%);  
          transform: translate(-50%, -50%);  
  text-align:center;
}

.redTurn{
  background-color:red;
  height: 80px;
  width: 80px;
  border: green 1px solid;
  border-radius: 40%;
  position: relative; 

   /* Rotation Animation */
  -webkit-animation: coin-rotate 2s infinite linear;
          animation: coin-rotate 2s infinite linear;
}

.yellowTurn{
  background-color:yellow;
  height: 80px;
  width: 80px;
  border: green 1px solid;
  border-radius: 40%;
  position: relative;
  left: 85%;

  /* Rotation Animation */
  -webkit-animation: coin-rotate 2s infinite linear;
          animation: coin-rotate 2s infinite linear; 
}

 /* Rotation Animation */
@-webkit-keyframes coin-rotate {
  0 { -webkit-transform: rotateY(0); transform: rotateY(0); }
  100% { -webkit-transform: rotateY(360deg); transform: rotateY(360deg); }   
}
@keyframes coin-rotate {
  0 { -webkit-transform: rotateY(0); transform: rotateY(0); }
  100% { -webkit-transform: rotateY(360deg); transform: rotateY(360deg); }   
}

.redWinner{
  background-color:red;
  height: 80px;
  width: 80px;
  border: green 1px solid;
  border-radius: 40%;
  position: relative; 
  left: 43%;
}

.trophyIcon{
  /* Blink Animation */
  -webkit-animation: flickerAnimation 1s infinite;
  animation: flickerAnimation 1s infinite;
}

.yellowWinner{
  background-color:yellow;
  height: 80px;
  width: 80px;
  border: green 1px solid;
  border-radius: 40%;
  position: relative; 
  left: 43%;
}

/* Blink Animation */
@keyframes flickerAnimation {
  0%   { opacity:1; }
  50%  { opacity:0; }
  100% { opacity:1; }
  }
  @-webkit-keyframes flickerAnimation{
  0%   { opacity:1; }
  50%  { opacity:0; }
  100% { opacity:1; }
  }




